.#{$theme} {
  &.tip-select {
    &.active {
      .select-label {
        border-color: $color-primary;
      }
    }
    &:not([disabled]):hover {
      .select-label {
        border-color: $color-primary;
      }
    }
  }

  .select-label {
    padding: 0 $gutter*2;
    border: 1px solid $color-border;
    //transition: all 0.2s;
    //cursor: pointer;
    background-color: $color-panel-bg;
    border-radius: $border-radius;
    //@include clearfix;
  }
}



