.#{$theme} {
  &.radio {
    .__icon {
      border-radius: $gutter*2;
      &:before {
        width: $gutter;
        height: $gutter;
        margin-left: -$gutter*0.5;
        margin-top: -$gutter*0.5;
        border-radius: $gutter*2;
        background-color: #fff;
      }
    }
  }
}



