.#{$theme} {
  &.btn {
    border-radius: $border-radius;
    padding: 0 8px;
  }
  &.btn-group {
    display: inline-block;
    border-radius: $border-radius;
    background-color: #fff;
    word-spacing: -0.2rem;
    vertical-align: middle;
    overflow: hidden;
    > .btn {
      word-spacing:0;
      border-radius: 0;
      &.active {
        background-color: $color-primary;
        color: #fff;
      }
    }
  }
}



