.#{$theme} {
  &.switch {
    .__icon {
      border-radius: $gutter*2;
      width: $gutter*4;
      &:before {
        width: $gutter*2;
        height: $gutter*2;
        border-radius: $gutter*2;
        opacity: 1;
        background-color: #fff;
        left:0;
        margin-top: -$gutter;
        box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
      }
    }
    .__input:checked {
      + .__label-group {
        .__icon {
          &:before {
            left: 100%;
            margin-left: -$gutter*2 + 1px;
            //background-color: $color-primary;
          }
        }
      }
    }
  }
}



