@import "../stylesheets/mixins";

$theme:'theme-dft';
$color-info:#2196f3 !default;
$color-success:#4caf50 !default;;
$color-warning:#F7B824 !default;;
$color-error:#FF5722 !default;;
$color-primary:#5FB878 !default;;

$color-theme:(
  'primary': $color-primary,
  'info': $color-info,
  'success': $color-success,
  'error': $color-error,
  'warning': $color-warning
) !default;

$color-border: #e0e0e0;
$color-grey-bg: #f5f5f5;
$color-hover: #eeeeee;
$color-panel-bg: #fff;
$color-disabled-bg: #f5f5f5;
$color-striped-bg:$color-grey-bg;

$gutter:8px;
$block-base-height: $gutter * 4;
$border-radius: $gutter / 2;

$font-size:20;
$font-family:Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;



