.#{$theme} {
  .tab-line {
    height: 2px;
    background-color: $color-primary;
  }

  &.tab-theme-default {
    .tab-label-group {
      border-bottom:1px solid $color-border;
    }
    .tab-label {
      padding: $gutter;
      margin-right: $gutter;
      font-weight: 500;
      &.active {
        color: $color-primary;
      }
    }
  }

  &.tab-theme-block {
    .tab-label {
      border: 1px solid $color-border;
      border-radius: $border-radius * 9;
      padding: $gutter*0.5 $gutter*2;
      margin-right: $gutter;
      &.active {
        background-color: $color-primary;
        color: #fff;
      }
    }
  }

}



