.#{$theme} {
  &.label-inp-group {
    .__icon {
      width: $gutter * 2;
      height: $gutter *2;
      border: 1px solid $color-border;
      background-color: #fff;
      transition: all 0.3s;
      &:before {
        opacity: 0;
        transition: all 0.3s;
      }
    }
    &:not([disabled]):not(.switch):hover {
      .__icon {
        border-color: $color-primary;
        background-color: $color-primary;
      }
    }
    .__input:checked {
      + .__label-group {
        .__icon {
          border-color: $color-primary;
          background-color: $color-primary;
          &:before {
            opacity: 1;
          }
        }
      }
    }
  }
}



