.#{$theme} {
  &.checkbox {
    .__icon {
      &:before {
        width: $gutter * 0.8;
        height: $gutter * 1.4;
        margin-left: -$gutter*0.4;
        margin-top: -$gutter*0.8;
        border-right: $gutter/4 solid #fff;
        border-bottom: $gutter/4 solid #fff;
        transform: rotate(30deg);
      }
    }
  }
}



