.#{$theme} {
  &.textarea-group {
    &.active {
      .textarea {
        border-color: $color-primary;
      }
    }
    &[disabled] {
      &:after {
        opacity: 0.5;
        background-color: $color-grey-bg;
      }
    }
    &:not([disabled]):hover {
      .textarea {
        border-color: $color-primary;
      }
    }
  }
  .textarea {
    //display: inline-block;
    padding: $gutter $gutter*2;
    //width: 100%;
    border: 1px solid $color-border;
    //outline: none;
    //transition: all 0.2s;
    //min-height: 100px;
    //font-family: $font-family;
    border-radius: $border-radius;
  }
}



