.#{$theme} {
  &.popper-confirm {
    background-color: #fff;
    width: 80%;
    border-radius: $border-radius;
    padding: 0 $gutter*2;
    .popper-head {
      height: $gutter*7;
      line-height: $gutter*7;
    }
    .popper-body {}
    .popper-foot {
      height: $gutter*6;
      line-height: $gutter*6;
      text-align: right;
      > .btn {
        margin-left: $gutter;
      }
    }
  }
  .popper-close {
    right: $gutter*2;top: $gutter*2;
  }

  &.popper-toast {
    border-radius: $border-radius;
    padding: $gutter*0.5 $gutter;
    .popper-body {
      color: #fff;
    }
  }

  &.popper-tip {
    color: #000;
    border-radius: $border-radius;
    padding: $gutter*0.5 $gutter;
    .popper-body {
      color: #fff;
    }
  }
}



