.#{$theme} {
  &.table {
    background-color: $color-panel-bg;

    &.bordered > tbody > tr {
      border-bottom: 1px solid $color-border;
    }

    &.striped > tbody td {
      border-radius: 0;
    }

    &.striped > tbody {
      > tr:nth-child(odd) {
        background-color: $color-striped-bg;
      }
    }

    &.hover > tbody > tr {
      &:hover {
        background-color: $color-hover;
      }
    }

    thead {
      border-bottom: 1px solid $color-border;
    }

    td, th {
      padding: $gutter*2 $gutter*0.5;
    }
  }
}





